/* components/SetupProfileContainer.js: */
.SetupProfilePics {
  display: flex;
  width: 100vw;
  margin-bottom: 2rem;
}

div.SetupProfilePics > div.col-4 {
  display: flex;
  flex-direction: column;
}

form.FormSetupProfile {
  max-width: 80vw;
}

div.FormFieldSetupProfile {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.FormHeader {
  color: white;
  font-size: 0.7em;
  font-weight: 600;
  margin-bottom: 0.3rem;
  margin-top: 0;
}

.FullName {
  display: inline;
}

.DisplayFullNameWrapper {
  margin-top: -1.2rem;
}

.CheckboxFullName {
  display: inline;
}

label.DisplayFullName {
  color: white;
  font-size: 0.7rem;
}

div.ArtFormsLookingForCollection {
  display: flex;
  flex-direction: column;
}

div.ArtFormSingle {
  border: 1px solid white;
  border-radius: 1rem;
  padding-right: 0.3rem;
  padding-left: 0.3rem;
}

div.ArtFormLookingForSingle {
  display: flex;
  flex-direction: row;
  border: 1px solid white;
  border-radius: 1rem;
  padding-right: 0.3rem;
  padding-left: 0.3rem;
}

p.ArtFormsText {
  font-size: 0.8rem;
  color: white;
  margin-bottom: 0;
}

img.RemoveArtForm {
  margin-left: 0.2rem;
}

div.tempGallery {
  width: 100vw;
  height: 66vw;
  background-color: rgb(0, 255, 191);
}

div.FooterContainerFour {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: 100px;
  grid-gap: 20px;
  margin-bottom: 2rem;
}

div.FooterContainerFour > div {
  align-self: end;
  justify-self: center;
}

/* end of SetupProfilePics.js */

/* components/SetupProfileContainer.js: */

form.SearchConnections {
  margin-top: 1rem;
}

form.SearchConnections > input {
  background: rgba(0, 0, 0, 0);
  border: 1px solid white;
  border-radius: 0.5rem;
  margin-right: 0.3rem;
}

form.SearchConnections > button {
  background: rgba(0, 0, 0, 0);
  border: 1px solid white;
  border-radius: 0.5rem;
  font-size: 1rem;
  color: white;
}

div.ConnectionsContainer {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 1rem;
}

div.ConnectionSingle {
  width: 60%;
  justify-self: center;
  margin-bottom: 1rem;
}

div.ConnectionSingle > img {
  width: 100%;
  height: auto;
  border-radius: 50%;
  margin-bottom: 0.2rem;
  box-shadow: 0px 0px 12px rgba(0, 0, 0, 1);
}

div.ConnectionSingle > img.OnlineConnection {
  border: 3px solid white;
}

div.ConnectionSingle > p {
  color: white;
  font-size: 0.7rem;
  margin: 0;
  text-align: center;
}

/* End of: SetupProfileContainer.js*/

